'use client';

import { ReactElement, useMemo } from 'react';
import { useIntl } from 'react-intl';

import { SearchType } from '@yandex-music-int/resource-collection';
import { ScrollableContent } from '@yandex-music-int/sol/components/ScrollableContent';

import { SendSearchFeedbackArg } from '@/shared/lib/index.js';
import { Footer } from '@/shared/ui/index.js';

import { SearchItemModel } from '../../model/SearchItem.js';
import { SearchResultBlock } from '../SearchResultBlock/SearchResultBlock.js';

import styles from './SearchMixed.module.scss';

interface Props {
    results: SearchItemModel[];
    isLoading: boolean;
    sendSearchFeedback?: (item: SendSearchFeedbackArg) => void;
}

interface SearchBlock {
    messageId: string;
    type: SearchType;
    items: SearchItemModel[];
}

const SearchMixedShimmer = (): ReactElement[] => {
    const items = Array.from(Array(2).keys());
    return items.map((index: number) => {
        return <SearchResultBlock key={index} isLoading />;
    });
};

export const SearchMixed = ({ results, isLoading, sendSearchFeedback }: Props): ReactElement => {
    const { formatMessage } = useIntl();

    const items = useMemo(() => {
        if (isLoading) {
            return <SearchMixedShimmer />;
        }

        const blocks: Record<string, SearchBlock> = {
            [SearchType.ALL]: {
                messageId: 'search-results.best',
                type: SearchType.ALL,
                items: []
            },
            [SearchType.ARTIST]: {
                messageId: 'search-results.artist',
                type: SearchType.ARTIST,
                items: []
            },
            [SearchType.ALBUM]: {
                messageId: 'search-results.album',
                type: SearchType.ALBUM,
                items: []
            },
            [SearchType.PLAYLIST]: {
                messageId: 'search-results.playlist',
                type: SearchType.PLAYLIST,
                items: []
            }
        };

        results.forEach((item: SearchItemModel) => {
            blocks[item.type]?.items.push(item);
            blocks[SearchType.ALL]?.items.push(item);
        });

        return Object.getOwnPropertyNames(blocks)
            .filter((blockName: string) => {
                const block = blocks[blockName] as SearchBlock;
                return block.items.length > 0;
            })
            .map((blockName: string, index: number) => {
                const block = blocks[blockName] as SearchBlock;
                return (
                    <SearchResultBlock
                        blockPosition={index}
                        key={index}
                        items={block.items}
                        title={formatMessage({ id: block.messageId })}
                        sendSearchFeedback={sendSearchFeedback}
                    />
                );
            });
    }, [isLoading, results, formatMessage, sendSearchFeedback]);

    return (
        <ScrollableContent className={styles.root} containerClassName={styles.container}>
            <div className={styles.content}>{items}</div>
            <Footer className={styles.footer} />
        </ScrollableContent>
    );
};
