'use client';

import { ReactElement, useMemo } from 'react';

import { SearchType } from '@yandex-music-int/resource-collection';
import { ScrollableContent } from '@yandex-music-int/sol/components/ScrollableContent';

import { AlbumCard } from '@/features/album/index.js';
import { ArtistCard } from '@/features/artist/index.js';
import { PlaylistCard } from '@/features/playlist/index.js';

import { SearchFeedbackContext, SendSearchFeedbackArg } from '@/shared/lib/index.js';
import { Footer, GridShimmer } from '@/shared/ui/index.js';

import { SearchItemModel } from '../../model/SearchItem.js';

import styles from './SearchEntitiesGrid.module.scss';

interface Props {
    results: SearchItemModel[];
    filter: string;
    isLoading: boolean;
    sendSearchFeedback?: (item: SendSearchFeedbackArg) => void;
}

export const SearchEntitiesGrid = ({ results, filter, isLoading, sendSearchFeedback }: Props): ReactElement => {
    const items = useMemo(() => {
        if (isLoading) {
            return (
                <GridShimmer
                    itemClassName={styles.block}
                    round={filter === SearchType.ARTIST}
                    centered={filter === SearchType.ARTIST}
                    count={36}
                />
            );
        }

        return results
            .filter((item: SearchItemModel) => item.type === filter)
            .map((item: SearchItemModel, index: number) => {
                switch (item.type) {
                    case SearchType.ALBUM:
                        return (
                            <SearchFeedbackContext.Provider
                                value={{
                                    sendSearchFeedback,
                                    id: item.data.id,
                                    type: SearchType.ALBUM,
                                    blockPosition: 0,
                                    position: index
                                }}
                            >
                                <AlbumCard key={item.data.id} album={item.data} />
                            </SearchFeedbackContext.Provider>
                        );
                    case SearchType.PLAYLIST:
                        return (
                            <SearchFeedbackContext.Provider
                                value={{
                                    sendSearchFeedback,
                                    id: item.data.uuid,
                                    type: SearchType.PLAYLIST,
                                    blockPosition: 0,
                                    position: index
                                }}
                            >
                                <PlaylistCard key={item.data.key} playlist={item.data} />
                            </SearchFeedbackContext.Provider>
                        );
                    case SearchType.ARTIST:
                        return (
                            <SearchFeedbackContext.Provider
                                value={{
                                    sendSearchFeedback,
                                    id: item.data.id,
                                    type: SearchType.ARTIST,
                                    blockPosition: 0,
                                    position: index
                                }}
                            >
                                <ArtistCard key={item.data.id} artist={item.data} />
                            </SearchFeedbackContext.Provider>
                        );
                    default:
                        return null;
                }
            });
    }, [isLoading, results, filter, sendSearchFeedback]);
    return (
        <ScrollableContent className={styles.root} containerClassName={styles.container}>
            <div className={styles.content}>{items}</div>
            <Footer className={styles.footer} />
        </ScrollableContent>
    );
};
