'use client';

import { ReactElement, useCallback, useMemo } from 'react';

import { YaMusicContextData, YaMusicContexts } from '@yandex-music-int/player-contexts';
import { MusicEntityData } from '@yandex-music-int/player-entities';
import { SearchType } from '@yandex-music-int/resource-collection';
import { ScrollableContent } from '@yandex-music-int/sol/components/ScrollableContent';
import { PlayContextParams } from '@yandex-music-int/sonata';

import { TrackPlaylist } from '@/features/track/index.js';

import { SearchFeedbackContext, SendSearchFeedbackArg, TrackVariant, useCreateFrom } from '@/shared/lib/index.js';
import { Footer, TrackShimmer } from '@/shared/ui/index.js';

import { SearchItemModel, SearchTrackModel } from '../../model/SearchItem.js';

import styles from './SearchTrackList.module.scss';

interface Props {
    results: SearchItemModel[];
    isLoading: boolean;
    containerClassName?: string;
    sendSearchFeedback?: (item: SendSearchFeedbackArg) => void;
}

const SearchTrackListShimmer = (): ReactElement[] => {
    const items = Array.from(Array(36).keys());
    return items.map((index: number) => (
        <TrackShimmer className={styles.shimmer} key={index} variant={TrackVariant.PLAYLIST} />
    ));
};

export const SearchTrackList = ({ results, isLoading, sendSearchFeedback }: Props): ReactElement => {
    const from = useCreateFrom();
    const getPlayContextParams = useCallback(
        (trackId: string): PlayContextParams<YaMusicContextData, MusicEntityData> => {
            return {
                contextData: {
                    type: YaMusicContexts.Various,
                    meta: {
                        id: trackId
                    },
                    from
                },
                loadContextMeta: true
            };
        },
        [from]
    );

    const items = useMemo(() => {
        if (isLoading) {
            return <SearchTrackListShimmer />;
        }

        return results
            .filter((item: SearchItemModel) => [SearchType.TRACK, SearchType.UGC_TRACK].includes(item.type))
            .map((item: SearchItemModel, index: number) => {
                const { data: track } = item as SearchTrackModel;
                return (
                    <SearchFeedbackContext.Provider
                        key={track.id}
                        value={{
                            sendSearchFeedback,
                            id: track.id,
                            type: SearchType.TRACK,
                            blockPosition: 0,
                            position: index
                        }}
                    >
                        <TrackPlaylist 
                            track={track} 
                            playContextParams={getPlayContextParams(track.id)} 
                            shouldCheckSelfIndex={false}
                        />
                    </SearchFeedbackContext.Provider>
                );
            });
    }, [isLoading, results, getPlayContextParams, sendSearchFeedback]);

    return (
        <ScrollableContent className={styles.root} containerClassName={styles.container}>
            <div className={styles.content}>{items}</div>
            <Footer className={styles.footer} />
        </ScrollableContent>
    );
};
