'use client';

import { clsx } from 'clsx';
import { ReactElement, forwardRef, Ref } from 'react';

import { SearchType } from '@yandex-music-int/resource-collection';

import { AlbumCard } from '@/features/album/index.js';
import { ArtistCard } from '@/features/artist/index.js';
import { PlaylistCard } from '@/features/playlist/index.js';
import { TrackCard } from '@/features/track/index.js';

import { SearchFeedbackContext, SendSearchFeedbackArg } from '@/shared/lib/index.js';
import { CarouselBlock } from '@/shared/ui/index.js';

import { SearchItemModel } from '../../model/SearchItem.js';

import styles from './SearchResultBlock.module.scss';

interface Props {
    items?: SearchItemModel[];
    title?: string;
    isLoading?: boolean;
    forwardRef?: Ref<HTMLDivElement>;
    sendSearchFeedback?: (item: SendSearchFeedbackArg) => void;
    blockPosition?: number;
}

const SearchResultBlockComponent = ({
    items = [],
    title,
    forwardRef: ref,
    isLoading = false,
    sendSearchFeedback,
    blockPosition = 0
}: Props): ReactElement => {
    return (
        <CarouselBlock
            showHeaderShimmer
            isLoading={isLoading}
            className={clsx(styles.root, { [styles.root_isLoading]: isLoading })}
            ref={ref}
            title={title}
            headerClassName={clsx(styles.header, styles.important)}
            containerClassName={clsx(styles.content, styles.important)}
        >
            {items.map((item: SearchItemModel, index: number) => {
                switch (item.type) {
                    case SearchType.ALBUM:
                        return (
                            <SearchFeedbackContext.Provider
                                key={item.data.id}
                                value={{
                                    sendSearchFeedback,
                                    id: item.data.id,
                                    type: SearchType.ALBUM,
                                    blockPosition,
                                    position: index
                                }}
                            >
                                <AlbumCard album={item.data} contentLinesCount={3} />
                            </SearchFeedbackContext.Provider>
                        );
                    case SearchType.ARTIST:
                        return (
                            <SearchFeedbackContext.Provider
                                key={item.data.id}
                                value={{
                                    sendSearchFeedback,
                                    id: item.data.id,
                                    type: SearchType.ARTIST,
                                    blockPosition,
                                    position: index
                                }}
                            >
                                <ArtistCard artist={item.data} contentLinesCount={3} />
                            </SearchFeedbackContext.Provider>
                        );
                    case SearchType.PLAYLIST:
                        return (
                            <SearchFeedbackContext.Provider
                                key={item.data.key}
                                value={{
                                    sendSearchFeedback,
                                    id: item.data.uuid,
                                    type: SearchType.PLAYLIST,
                                    blockPosition,
                                    position: index
                                }}
                            >
                                <PlaylistCard playlist={item.data} contentLinesCount={3} />
                            </SearchFeedbackContext.Provider>
                        );
                    case SearchType.UGC_TRACK:
                    case SearchType.TRACK:
                        return (
                            <SearchFeedbackContext.Provider
                                key={item.data.id}
                                value={{
                                    sendSearchFeedback,
                                    id: item.data.id,
                                    type: SearchType.TRACK,
                                    blockPosition,
                                    position: index
                                }}
                            >
                                <TrackCard track={item.data} />
                            </SearchFeedbackContext.Provider>
                        );
                }
            })}
        </CarouselBlock>
    );
};

export const SearchResultBlock = forwardRef<HTMLDivElement, Props>((props: Props, ref: Ref<HTMLDivElement>) => (
    <SearchResultBlockComponent forwardRef={ref} {...props} />
));
