import { types, Instance } from 'mobx-state-tree';

import { SearchType } from '@yandex-music-int/resource-collection';

import { AlbumWithArtists } from '@/features/album/index.js';
import { PlaylistWithArtists } from '@/features/playlist/index.js';
import { TrackWithArtists, UgcTrackWithArtists } from '@/features/track/index.js';

import { Artist } from '@/entities/artist/index.js';

export const SearchAlbum = types.model('SearchAlbum', {
    type: types.literal(SearchType.ALBUM),
    data: AlbumWithArtists
});

export const SearchPlaylist = types.model('SearchPlaylist', {
    type: types.literal(SearchType.PLAYLIST),
    data: PlaylistWithArtists
});

export const SearchArtist = types.model('SearchArtist', {
    type: types.literal(SearchType.ARTIST),
    data: Artist
});

export const SearchUgcTrack = types.model('SearchUgcTrack', {
    type: types.literal(SearchType.UGC_TRACK),
    data: UgcTrackWithArtists
});

export const SearchTrack = types.model('SearchTrack', {
    type: types.literal(SearchType.TRACK),
    data: TrackWithArtists
});

export const SearchItem = types.union(SearchAlbum, SearchPlaylist, SearchArtist, SearchTrack, SearchUgcTrack);

export type SearchAlbumModel = Instance<typeof SearchAlbum>;
export type SearchArtistModel = Instance<typeof SearchArtist>;
export type SearchPlaylistModel = Instance<typeof SearchPlaylist>;
export type SearchTrackModel = Instance<typeof SearchTrack>;
export type SearchItemModel = Instance<typeof SearchItem>;
