'use client';

import { clsx } from 'clsx';
import { debounce } from 'lodash-es';
import { observer } from 'mobx-react-lite';
// @ts-expect-error        next,    es-modules
import { redirect } from 'next/navigation';
import { ReactElement, useCallback, useMemo, useState } from 'react';
import { useIntl } from 'react-intl';

import { SearchFilter } from '@yandex-music-int/resource-collection';
import { SearchInput } from '@yandex-music-int/sol/components/SearchInput';
import { useTabsState } from '@yandex-music-int/sol/components/Tabs';

import { SearchEmpty, SearchEntitiesGrid, SearchTrackList, SearchMixed } from '@/widgets/search/index.js';

import {
    AnalyticsPage,
    AnalyticsPageProvider,
    useStore,
    SendSearchFeedbackArg,
    Experiments
} from '@/shared/lib/index.js';
import { Footer, Tab, TabCarousel } from '@/shared/ui/index.js';

import { SEARCH_FILTERS } from '../lib/constants/SearchFilters.js';

import styles from './Search.module.scss';

const FILTER_CHANGE_DEBOUNCE_MS = 300;

export const SearchPage = observer((): ReactElement => {
    const tabsState = useTabsState(0);
    const [searchFilter, setSearchFilter] = useState(SearchFilter.TOP);
    const { formatMessage } = useIntl();
    const { search, experiments } = useStore();

    if (experiments.checkExperiment(Experiments.WebNextDisableSearch, 'on')) {
        redirect('/');
    }

    const [searchText, setSearchText] = useState('');
    const showFilters = searchText.length > 0;

    const getSearchResults = useCallback(
        (text: string, filter: SearchFilter) => {
            search.resetResults();

            const isEmptySearchText = text.length === 0;

            if (!isEmptySearchText) {
                search.getSearchResults({ text: decodeURIComponent(text), filter });
            }
        },
        [search]
    );

    const handleSearchChange = useCallback(
        (value: string) => {
            const trimmedValue = value.trim();
            const text = encodeURIComponent(trimmedValue);
            setSearchText(text);
            getSearchResults(text, searchFilter);
        },
        [getSearchResults, searchFilter]
    );

    const handleFilterClick = useMemo(() => {
        return debounce((value: number) => {
            if (!tabsState.onTabChange || value === tabsState.value) {
                return;
            }

            tabsState.onTabChange(value);
            const filter = SEARCH_FILTERS[value] ?? SearchFilter.TOP;
            setSearchFilter(filter);
            getSearchResults(searchText, filter);
        }, FILTER_CHANGE_DEBOUNCE_MS);
    }, [getSearchResults, searchText, tabsState]);

    const sendSearchFeedback = useMemo(() => {
        return ({ id, type, blockPosition, position, feedbackType }: SendSearchFeedbackArg) => {
            search.sendFeedback({
                blockType: type,
                entityId: `${type}:${id}`,
                timestamp: new Date().toISOString(),
                searchRequestId: search.searchRequestId,
                query: searchText,
                clickType: feedbackType,
                blockPosition,
                position,
                page: 0 //     pager   
            });
        };
    }, [search, searchText]);

    const searchResults = useMemo(() => {
        if (!search.isLoading && search.results.length === 0) {
            return <SearchEmpty />;
        }
        switch (searchFilter) {
            case SearchFilter.TOP:
                return (
                    <SearchMixed
                        isLoading={search.isLoading}
                        results={search.results}
                        sendSearchFeedback={sendSearchFeedback}
                    />
                );
            case SearchFilter.TRACK:
                return (
                    <SearchTrackList
                        isLoading={search.isLoading}
                        results={search.results}
                        sendSearchFeedback={sendSearchFeedback}
                    />
                );
            case SearchFilter.ALBUM:
                return (
                    <SearchEntitiesGrid
                        isLoading={search.isLoading}
                        results={search.results}
                        filter={SearchFilter.ALBUM}
                        sendSearchFeedback={sendSearchFeedback}
                    />
                );
            case SearchFilter.PLAYLIST:
                return (
                    <SearchEntitiesGrid
                        isLoading={search.isLoading}
                        results={search.results}
                        filter={SearchFilter.PLAYLIST}
                        sendSearchFeedback={sendSearchFeedback}
                    />
                );
            case SearchFilter.ARTIST:
                return (
                    <SearchEntitiesGrid
                        isLoading={search.isLoading}
                        results={search.results}
                        filter={SearchFilter.ARTIST}
                        sendSearchFeedback={sendSearchFeedback}
                    />
                );
            default:
                return null;
        }
    }, [search.isLoading, search.results, searchFilter, sendSearchFeedback]);

    const content = useMemo(() => {
        if (searchText.length > 0) {
            return searchResults;
        }

        return (
            <div className={styles.main}>
                <Footer className={styles.footer} />
            </div>
        );
    }, [searchResults, searchText.length]);

    return (
        <AnalyticsPageProvider pageId={AnalyticsPage.SEARCH}>
            <div
                className={clsx(styles.root, {
                    [styles.root_showFilters]: showFilters
                })}
            >
                <div className={styles.header}>
                    <SearchInput
                        autoFocus
                        initialValue={searchText}
                        placeholder={formatMessage({ id: 'search.input-placeholder' })}
                        onChange={handleSearchChange}
                        resetButtonAriaLabel={formatMessage({ id: 'interface-actions.reset-search-input' })}
                    />
                    {showFilters && (
                        <TabCarousel className={styles.tabCarousel} {...tabsState} onTabChange={handleFilterClick}>
                            {SEARCH_FILTERS.map((filter: string, index: number) => {
                                return (
                                    <Tab
                                        className={clsx(styles.filter, {
                                            [styles.filter_selected]: index === tabsState.value
                                        })}
                                        title={formatMessage({ id: `search-filters.${filter}` })}
                                        value={index}
                                        key={index}
                                    />
                                );
                            })}
                        </TabCarousel>
                    )}
                </div>
                <div className={styles.content}>{content}</div>
            </div>
        </AnalyticsPageProvider>
    );
});
